body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.outerbox{
  margin: 10px;
  border: solid 1px black;
  height: 100%;
}
.header{
  position: absolute;
  top:0px;
}
.header p{
  margin-top : 10px;
}

.headerTitle{
  margin-top: 0px;
  background-color: teal;
  height : 50px;
}
.searchBox{
  margin : 30px;
  width : 65%;

}

.leftBox{
  border: 1px solid black;
  width: 46.5vw;
  height: auto;
  padding: 20px;
  float:left;
}

.recipeview{
  cursor:pointer;
  width: 75%;
  margin-left: 28px;
  margin-top: 15px;
  text-align: center;
  border: solid 2px black
}
.rightBox{
  float:left;
  border: 1px solid black;
  width: 45vw;
  height: auto;
}
.nameHeader{
  font-size:50px;
  font-weight: bolder;
  text-align: center;
}


.ingredients{
  list-style-type: square;
}

.instructions{
  list-style-type: square;
}
.ingreTitle{
  margin-top:30px;
  font-size:20px;
  font-weight: bold;
}

.instTitle{
  font-size:20px;
  font-weight: bold;
}

.foodspic{
  width: 128px;
  height: 128px;
  float: left;
  margin-left: 20px;
}
